}

.header {
    background-color: #000 !important;

}

.header .nav-link {
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    color: #fff !important;
    font-weight: 500;
    padding: 0.5rem 1rem !important;
}

.header_btn {
    background: #1a1b1f;
    color: #fff;
    border: 2px solid #c3a177;
    font-size: 13px !important;
    letter-spacing: 2px;
    margin-left: 70px;
    transition: 0.3s all ease-in-out;
}

.header_btn_hover:hover {
    background-color: #fff;
    color: #000 !important;
}
/* <!-- =================================== -->
<!-- ==============who_are_section page============= -->
<!-- =================================== --> */
.who_are_section{
padding: 100px 0 0 0;
}
.who_we_img_col{
    text-align: center;
}

.who_we_content_col h5{
    text-transform: uppercase;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    position: relative;
    letter-spacing: 0.09em;
    display: inline-block;
    padding: 0 15px;
    margin-bottom: 25px;
}

.who_we_content_col h5::before{
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 10px;
    height: 10px;
    background: #C3A177;
    border-radius: 50%;
}
.who_we_content_col h5::after{
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 10px;
    height: 10px;
    background: #C3A177;
    border-radius: 50%;


    left: auto;
    right: 0;
}

.who_we_content_col h1{
    color: #c3a177;
    font-size: 42px;
    margin-bottom: 30px;
    letter-spacing: -0.01em;
}
.color_span{
    color: #c3a177;
}
.who_we_content_col .btns{
padding: 12px 45px;
border-radius: 10px;
font-size: 18px;
font-weight: 600;
text-decoration: none;
/* background-color: #fff; */
color: #fff;
border: 2px solid #c3a177;
letter-spacing: 2px;
transition: 0.3s all ease-in-out;
max-width: 300px;
}
.docs_list{
    margin-top: 30px;
    margin-bottom: 50px;
}
.docs_list li{
    font-size: 18px;
    color: #fff;
    position: relative;
    margin-bottom: 20px;
    padding-left: 10px;
}

/* <!-- =================================== -->
<!-- ==============Join_Future_section page============= -->
<!-- =================================== --> */

.Join_Future_section{
padding: 100px 0 0 0;

}
.Join_Future_section h1{
    font-size: 55px;
    margin-bottom: 70px;
    line-height: 1.4;
    letter-spacing: -0.01em;
    color: #c3a177 ;
    text-align: center;

}

.progress_heading{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.progress_heading_number{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.progress_heading h3{
    font-weight: 600;
    font-size: 12px;
    text-transform: uppercase;
    color: #C3A177;
    display: inline-block;
    position: relative;
    padding-bottom: 25px;
}
.progress_heading .second{
    color: #FF9700;
}

.progress_heading .third{
    color: #12D176;
}
.progress_heading .first::before{
    content: "";
    position: absolute;
    bottom: 6px;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 14px;
    background-color: #C3A177;
}
.progress_heading .second::before{
    content: "";
    position: absolute;
    bottom: 6px;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 14px;
    background-color: #FF9700;
}
.progress_heading .third::before{
    content: "";
    position: absolute;
    bottom: 6px;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 14px;
    background-color: #12D176;

}
.progress_heading_number h3{
    font-size: 16px;
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 0;
    margin-top: 10px;
}
.progress-bar{
    background-color: #c3a177;
}

.borders{
    border: 1px solid rgba(255, 255, 255, 0.06);
    height: 123px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.borders img{
    opacity: 0.5;
    transition: 0.3s all ease-in-out;
}
.borders img:hover{
    opacity: 1;
    cursor: pointer;
}
